@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;700&display=swap");

.box {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.productHeading {
  font-family: 'Poppins', sans-serif;
  color: black;
  font-size: 2.5rem;
  font-weight: 400;
}

@media screen and (min-width: 768px) {
  .box {
    display: flex;
    flex-direction: row;
  }
}
