@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;700&display=swap");

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
  font-size: 16px;
}

body {
  font-family: "Poppins", sans-serif, "Segoe UI", Tahoma, Geneva, Verdana;
  font-weight: 500;
  outline: none;
}

h1,
h2,
h3,
h4 {
  color: #35323e;
  font-weight: 700;
}

p {
  color: #9e9aa7;
  font-weight: 500;
  line-height: 1.7;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

.btn {
  display: inline-block;
  color: white;
  background-color: #2acfcf;
  text-transform: capitalize;
  font-weight: 700;
  border: none;
  outline: none;
  cursor: pointer;
  border-radius: 100px;
  transition: 0.3s;
}

.btn.btn-sm {
  padding: 7px 21px;
}

.btn.btn-lg {
  padding: 12px 36px;
}

.btn.btn-plus-icon {
  display: flex;
  align-items: center;
  justify-content: center;
  column-gap: 10px;
}

.btn.btn-plus-icon .icon {
  font-size: 22px;
}

.btn:hover {
  background-color: #6be1e1;
}

.flex-between {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 40px;
}

.flex-ver-top {
  align-items: flex-start;
}

.scale-effect:active {
  transform: scale(0.88);
  transition: 0.2s;
}

.sectionHeader {
  text-align: center;
}

.sectionHeader h2 {
  font-size: 35px;
}

@media (max-width: 500px) {
  .sectionHeader h2 {
    font-size: 26px;
  }
}

@media (max-width: 280px) {
  .sectionHeader h2 {
    font-size: 22px;
  }
}

.sectionHeader p {
  width: 50%;
  margin: 7px auto 0;
}

@media (max-width: 992px) {
  .sectionHeader p {
    width: 70%;
  }
}

@media (max-width: 500px) {
  .sectionHeader p {
    width: 100%;
  }
}

.container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 40px;
  position: relative;
  width: 78%;
  margin: 0 auto;
}

@media (max-width: 992px) {
  .container {
    width: 90%;
  }
}

.header {
  padding: 45px 0;
}

.header .logo img {
  height: 30px;
}

.header .mainNavigation {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  gap: 40px;
  flex: 1;
}

@media (max-width: 992px) {
  .header .mainNavigation {
    position: absolute;
    top: 150%;
    left: 50%;
    transform: translateX(-50%);
    width: 70%;
    height: 0;
    z-index: 999;
    overflow: hidden;
    flex-direction: column;
    align-items: center;
    padding: 0 33.33333px;
    border-radius: 10px;
    background-color: #3b3054;
    transition: height 0.3s;
  }
}

@media (max-width: 767px) {
  .header .mainNavigation {
    width: 90%;
  }
}

@media (max-width: 375px) {
  .header .mainNavigation {
    width: 100%;
  }
}

@media (max-width: 280px) {
  .header .mainNavigation {
    padding: 0 25px;
  }
}

.url-shorten-form > div {
  position: relative;
  flex: 1;
}

.header .mainNavigation > div {
  display: flex;
  align-items: center;
  gap: 30px;
}

@media (max-width: 992px) {
  .header .mainNavigation > div {
    flex-direction: column;
    width: 100%;
    padding: 33.33333px 0;
  }
}

@media (max-width: 280px) {
  .header .mainNavigation > div {
    padding: 25px 0;
  }
}

.header .mainNavigation div:first-child {
  border-bottom: 1px solid white;
}

.url-shorten-form .btn {
  text-align: center;
  border-radius: inherit;
  white-space: nowrap;
  font-size: 16px;
  cursor: pointer;
}

.header .nav-buttons .btn {
  color: #fff;
  width: 80%;
}

@media (max-width: 375px) {
  .header .nav-buttons .btn {
    width: 100%;
  }
}

.header .burgerMenu {
  font-size: 22px;
  color: #9e9aa7;
  cursor: pointer;
  display: none;
}

@media (max-width: 992px) {
  .header .burgerMenu {
    display: block;
  }
}

.mainNavigation a {
  font-weight: 700;
  font-size: 14px;
  color: #9e9aa7;
  transition: 0.3s;
}

.mainNavigation a:hover {
  color: #232127;
}

@media (max-width: 992px) {
  .mainNavigation a {
    width: 100%;
    color: white;
    text-align: center;
    font-size: 16px;
  }

  .mainNavigation a:hover {
    color: #2acfcf;
  }
}

.landing {
  display: flex;
  align-items: center;
  padding: 40px 0;
  margin-left: 11%;
  overflow-x: hidden;
}

@media (max-width: 992px) {
  .landing {
    flex-direction: column-reverse;
    margin-left: 0;
    padding-bottom: 80px;
    row-gap: 60px;
  }
}

@media (max-width: 280px) {
  .landing {
    padding-bottom: 40px;
  }
}

.landing .landingText {
  flex: 1;
  min-width: 465px;
}

@media (max-width: 992px) {
  .landing .landingText {
    max-width: 95%;
    min-width: 0;
    text-align: center;
  }
}

.landing .landingText h1 {
  width: 100%;
  font-size: 3.9em;
  line-height: 1.15;
}

@media (max-width: 992px) {
  .landing .landingText h1 {
    font-size: 3em;
  }
}

@media (max-width: 500px) {
  .landing .landingText h1 {
    font-size: 2em;
  }
}

.landing .landingText p {
  font-size: 18px;
  max-width: 500px;
  margin: 0 0 29.41176px;
}

@media (max-width: 992px) {
  .landing .landingText p {
    font-size: 16px;
    margin: 10px auto 29.41176px;
  }
}

@media (max-width: 500px) {
  .landing .landingText p {
    font-size: 15px;
  }
}

.landing .landingImage {
  background-color: #fff;
  position: relative;
  right: 100px;
}

@media (max-width: 992px) {
  .landing .landingImage {
    flex-basis: initial;
    right: -240px;
  }
}

@media (max-width: 767px) {
  .landing .landingImage {
    right: -170px;
  }
}

@media (max-width: 500px) {
  .landing .landingImage {
    right: -110px;
  }
}

@media (max-width: 375px) {
  .landing .landingImage {
    right: -120px;
  }
}

@media (max-width: 280px) {
  .landing .landingImage {
    right: -70px;
  }
}

@media (min-width: 1100px) {
  .landing .landingImage {
    right: -130px;
  }
}

.landing .landingImage img {
  width: 100%;
}

.features {
  margin-top: 100px;
  background-color: #eff0f5;
}

.url-shorten-form {
  width: 100%;
  display: flex;
  gap: 20px;
  flex-wrap: wrap;

  /* background: #3b3054 url(../images/bg-shorten-desktop.svg) no-repeat right top; */
  background-size: cover;
  padding: 45px;
  border-radius: 6px;
  margin-bottom: 20px;
  transform: translateY(-50%);
  transition: gap 0.3s;
}

@media (max-width: 850px) {
  .url-shorten-form {
    flex-direction: column;

    /* background: #3b3054 url(../images/bg-shorten-mobile.svg) no-repeat right top; */
    padding: 25px;
    margin-bottom: 0;
  }
}

@media (max-width: 280px) {
  .url-shorten-form {
    padding: 20px;
  }
}

.url-shorten-form .url-input {
  width: 100%;
  font-family: "Poppins", sans-serif, "Segoe UI", Tahoma, Geneva, Verdana;
  font-size: 18px;
  padding: 8px 20px;
  background: white;
  border: 2px solid transparent;
  border-radius: 6px;
  outline: none;
  transition: 0.2s;
}

.url-shorten-form .url-input::placeholder {
  font-family: "Poppins", sans-serif, "Segoe UI", Tahoma, Geneva, Verdana;
  font-size: 16px;
  font-weight: 500;
  color: #9e9aa7;
}

.url-shorten-form .alert {
  position: absolute;
  left: 0;
  top: calc(100% + 4px);
  font-style: italic;
  font-size: 16px;
  color: #f46262;
  opacity: 0;
  transition: 0.2s opacity;
}

@media (max-width: 500px) {
  .url-shorten-form .alert {
    font-size: 14px;
  }
}

@media (max-width: 850px) {
  .url-shorten-form .btn {
    font-size: 19px;
    padding: 10px 30px;
  }
}

.url-shorten-form.empty {
  gap: 37px;
}

.url-shorten-form.empty .url-input {
  border-color: #f46262;
}

.url-shorten-form.empty .url-input::placeholder {
  color: #f46262;
  opacity: 0.5;
}

.url-shorten-form.empty .alert {
  opacity: 1;
}

.url-shorten-form.success button {
  background: #30c59b;
}

.url-shorten-results {
  position: relative;
  top: -60px;
}

.url-shorten-results .url-shorten-result {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 15px;
  flex-wrap: wrap;
  padding: 15px 25px;
  background: white;
  border-radius: 6px;
  margin-bottom: 15.15152px;
}

.url-shorten-result .old-url p {
  color: #35323e;
  font-weight: 500;
}

.feature .featureDetails p {
  margin-top: 10px;
  font-size: 14px;
}

.url-shorten-results .url-shorten-result > div p {
  font-size: 17px;
  word-break: break-word;
}

.url-shorten-result .old-url a {
  color: inherit;
}

.url-shorten-result .new-url a {
  color: #2acfcf;
}

.url-shorten-results .url-shorten-result > div a:hover {
  text-decoration: underline wavy #30c59b 2px;
}

@media (max-width: 850px) {
  .url-shorten-results .url-shorten-result {
    flex-direction: column;
    align-items: flex-start;
    padding: 0;
    gap: 0;
  }

  .url-shorten-results .url-shorten-result > div {
    width: 100%;
    align-items: flex-start;
    padding: 12px 18px;
  }
}

.url-shorten-results .delete-all-urls {
  display: block;
  margin: auto;
  font-size: 15px;
  background-color: #f24a4a;
}

.url-shorten-results .delete-all-urls:hover {
  background-color: #f46262;
}

@media (max-width: 850px) {
  .url-shorten-result .old-url {
    border-bottom: 1px solid #bfbfbf;
  }
}

.url-shorten-result .new-url {
  display: flex;
  align-items: center;
  gap: 20px;
}

@media (max-width: 850px) {
  .url-shorten-result .new-url {
    flex-direction: column;
    gap: 12px;
  }
}

.url-shorten-result .options {
  display: flex;
  gap: 10px;
}

@media (max-width: 850px) {
  .url-shorten-result .options {
    width: 100%;
  }
}

.url-shorten-result .options button {
  width: 95px;
  font-size: 15px;
  border-radius: 6px;
  cursor: pointer;
}

.url-shorten-result .options button.copied {
  background: #3b3054;
}

.url-shorten-result .options button.delete-url {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 35px;
  background: #f46262;
  border: none;
  color: white;
  font-size: 18px;
  transition: 0.2s;
}

.url-shorten-result .options button.delete-url:hover {
  color: #f46262;
  background-color: #eff0f5;
  font-size: 20px;
}

@media (max-width: 850px) {
  .url-shorten-result .options button {
    width: 100%;
    font-size: 18px;
    padding: 10px 30px;
  }
}

.moreFeaturesContent {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 25px;
  padding: 80px 0 90px 0;
  margin-left: 10%;
}

@media (max-width: 666px) {
  .moreFeaturesContent {
    gap: 80px;
  }
}

@media (max-width: 280px) {
  .moreFeaturesContent {
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  }
}

.moreFeaturesContent .feature {
  position: relative;
  margin-top: 50px;
  background: white;
  border-radius: 6px;
}

.moreFeaturesContent .feature::before,
.moreFeaturesContent .feature::after {
  position: absolute;
  content: "";
  background: #2acfcf;
}

@media (max-width: 666px) {
  .moreFeaturesContent > div:nth-child(2)::before,
  .moreFeaturesContent > div:nth-child(2)::after {
    width: 6px;
    height: 80px;
    left: 50%;
    transform: translateX(-50%);
  }

  .moreFeaturesContent > div:nth-child(2)::before {
    top: -80px;
  }

  .moreFeaturesContent > div:nth-child(2)::after {
    bottom: -80px;
  }
}

@media (min-width: 1130px) {
  .moreFeaturesContent {
    align-items: flex-start;
  }

  .moreFeaturesContent > div:nth-child(2) {
    margin-top: 30px;
  }

  .moreFeaturesContent > div:nth-child(2)::before,
  .moreFeaturesContent > div:nth-child(2)::after {
    height: 6px;
    width: 25px;
    top: 40%;
  }

  .moreFeaturesContent > div:nth-child(2)::before {
    left: -25px;
  }

  .moreFeaturesContent > div:nth-child(2)::after {
    right: -25px;
  }

  .moreFeaturesContent > div:nth-child(3) {
    margin-top: 60px;
  }
}

.feature .featureIllustration {
  position: absolute;
  top: -32.5px;
  left: 25px;
  display: grid;
  place-items: center;
  width: 65px;
  height: 65px;
  background: #3b3054;
  border-radius: 50%;
}

@media (max-width: 666px) {
  .feature .featureIllustration {
    left: 50%;
    transform: translateX(-50%);
    width: 80px;
    height: 80px;
  }
}

.feature .featureIllustration img {
  max-width: 50%;
}

.feature .featureDetails {
  padding: 0 25px 30px;
  margin-top: 60px;
}

@media (max-width: 666px) {
  .feature .featureDetails {
    text-align: center;
    margin-top: 80px;
  }
}

@media (max-width: 280px) {
  .feature .featureDetails {
    padding: 0 20px 25px;
  }
}

@media (max-width: 500px) {
  .feature .featureDetails h3 {
    font-size: 20px;
  }
}

@media (max-width: 280px) {
  .feature .featureDetails h3 {
    font-size: 18px;
  }
}
